              <p class="label">swl aux:</p>
              <!-- <label class="star-label"><i class="las la-asterisk"></i></label> -->
            </div>
            <input type="text" v-model="formData.swl_aux" placeholder="swl aux" />
          </div>
          <div class="input-set">
            <div class="label-box">
              <p class="label">Equipment Type:</p>
              <!-- <label class="star-label"><i class="las la-asterisk"></i></label> -->
            </div>
            <DxSelectBox
              style="border: 0; font-size: 14px"
              v-model="formData.id_equipment_type"
              :data-source="formSelect.id_equipment_type"
              display-expr="code"
              value-expr="id"
              placeholder="Select"
            />
          </div>
          <div class="input-set">
            <div class="label-box">
              <p class="label">Equipment User:</p>
              <!-- <label class="star-label"><i class="las la-asterisk"></i></label> -->
            </div>
            <input type="text" v-model="formData.eq_user" placeholder="Equipment User" />
          </div>
          <div class="input-set">
            <div class="label-box">
              <p class="label">Equipment Owner:</p>
              <!-- <label class="star-label"><i class="las la-asterisk"></i></label> -->
            </div>
            <input type="text" v-model="formData.eq_owner" placeholder="Equipment Owner" />
          </div>
          <div class="input-set">
            <div class="label-box">
              <p class="label">Model:</p>
              <!-- <label class="star-label"><i class="las la-asterisk"></i></label> -->
            </div>
            <input type="text" v-model="formData.model" placeholder="Model" />
          </div>

          <div class="input-set">
            <div class="label-box">
              <p class="label">Part line:</p>
              <label class="star-label">
