              <p class="label">License Plate No:</p>
              <label class="star-label">
                <i class="las la-asterisk"></i>
              </label>
            </div>
            <input type="text" v-model="formData.license_plate_no" placeholder="License Plate No" />
          </div>

          <div class="input-set">
            <div class="label-box">
              <p class="label">License Plate Province:</p>
              <label class="star-label">
                <i class="las la-asterisk"></i>
              </label>
            </div>
            <input
              type="text"
              v-model="formData.license_plate_province"
              placeholder="License Plate Province"
            />
          </div>

          <!-- <hr style="grid-column: span 3" /> -->
        </div>
      </div>
      <div class="popup-content loading-section" v-if="false">
        <contentLoading text="Loading, please wait..." color="#fc9b21" />
      </div>
      <div class="popup-footer">
        <div class="button-set">
          <button class="blue" v-on:click="SAVE()">
            <label>Save</label>
          </button>
          <button class="grey" v-on:click="CANCEL()">
            <label>Cancel</label>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "/axios.js";
import moment from "moment";
import DxSelectBox from "devextreme-vue/select-box";
//import DxDateBox from "devextreme-vue/date-box";
// import { DxLookup, DxDropDownOptions } from "devextreme-vue/lookup";
import contentLoading from "@/components/app-structures/app-content-loading.vue";

export default {
  name: "popup-add",
  components: {
    DxSelectBox,
    //DxDateBox,
    // DxLookup,
    // DxDropDownOptions,
    contentLoading
  },
  data() {
    return {
      formData: {},
      formSelect: {
        site: [],
        applicable_status: []
      }
    };
  },
  computed: {
    all_form_select_loaded() {
      if (
        this.formSelect.site.length > 0 &&
        this.formSelect.tube_material.length > 0 &&
        this.formSelect.material_of_const.length > 0 &&
        this.formSelect.applicable_status > 0
      ) {
        return true;
      } else return false;
    }
  },

  created() {
    this.FETCH_DROPDOWN();
  },
  methods: {
    SAVE() {
      console.log("Submit: ");

      this.formData.inservice_date = moment(
        this.formData.inservice_date
      ).format("L");
      //console.log(this.formData);
      if (
        this.formData.tag_no &&
        this.formData.id_site &&
        this.formData.serial_no !== ""
      ) {
        this.$ons.notification.confirm("Confirm save?").then(res => {
          if (res == 1) {
            const user = JSON.parse(localStorage.getItem("user"));
            this.formData.created_by = user.id;
            this.formData.updated_by = user.id;

            const data = this.formData;
            axios({
              method: "post",
              url: "/LiftingInfo",
              headers: {
                Authorization:
                  "Bearer " + JSON.parse(localStorage.getItem("token"))
              },
              data: data
            })
              .then(res => {
                // console.log(res.data[0]);
                if (res.status == 201) {
                  this.$emit("closePopup");
                  const id_tag = res.data[0].id_tag;
                  if (id_tag) {
                    this.$router.push("/lifting" + "/tag" + id_tag + "/info");
                  }
                }
              })
              .catch(error => {
                this.$ons.notification.alert(
                  error.code + " " + error.response.status + " " + error.message
                );
              })
              .finally(() => {});
          }
        });
      } else {
        this.$ons.notification.alert("Please fill all required fields.");
      }
    },
    CANCEL() {
      if (this.formData != this.formData) {
        this.$ons.notification
          .confirm("Your unsaved changes will be lost")
          .then(res => {
            if (res == 1) {
              this.$emit("closePopup");
            }
          });
      } else {
        this.$emit("closePopup");
      }
    },
    FETCH_DROPDOWN() {
      this.FETCH_DROPDOWN_SITE();
      this.FETCH_DROPDOWN_MATERIAL_OF_CONST();
      this.FETCH_DROPDOWN_APPLICABLE_STATUS();
      this.FETCH_DROPDOWN_EQUIPMENT_TYPE();
      this.FETCH_DROPDOWN_TUBE_MATERIAL();
    },
    FETCH_DROPDOWN_SITE() {
      axios({
        method: "get",
        url: "/master-data/get-md-site-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.site = res.data;
        }
      });
    },
    FETCH_DROPDOWN_APPLICABLE_STATUS() {
      axios({
        method: "get",
        url: "/master-data/get-md-applicable-status-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.applicable_status = res.data;
        }
      });
    },
    FETCH_DROPDOWN_TUBE_MATERIAL() {
      axios({
        method: "get",
        url: "master-data/get-md-tubematerial-status-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.tube_material = res.data;
        }
      });
    },
    FETCH_DROPDOWN_MATERIAL_OF_CONST() {
      axios({
        method: "get",
        url: "master-data/get-md-materialofconst-status-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.material_of_const = res.data;
        }
      });
    },
    FETCH_DROPDOWN_EQUIPMENT_TYPE() {
      axios({
        method: "get",
        url: "master-data/get-md-pvequipmenttype-status-list",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }).then(res => {
        if (res.data) {
          this.formSelect.material_of_const = res.data;
        }
      });
    }
  }
};
</script>

